// Id 2
const getMenuTree = ()=>{
		return getOneMenu(2).then(menu=>{
			//  path
			return new Promise((resolve,reject)=>{
				let menuArr;
				if(typeof menu === 'string'){
					menuArr = JSON.parse(menu);
				}else{
					menuArr = menu;
				}
				if(menuArr && menuArr.length>0){
					let menuTree = []; //menu
					let zeroTree = []; // childen
					let commonTree = []; // childen
					menuArr.forEach(v=>{
						if(v.pageId == 0 && v.tmpId == 0){
							zeroTree.push(v)
						}else if(v.pageId && v.pageId != 0){
							commonTree.push(v)
						}else{
							menuTree.push(v)
						}
					})
					
					menuTree.unshift({pageName:'',childen:commonTree,pageId:null,tmpId:null,icon:'common'})
					//
					if(menuTree.length>0){
						resolve(menuTree)
					}else{
						reject("")
					}
				}else{
					reject("")
				}
			})
		}).catch(error=>{
			uni.showToast({
				title:"",
				icon:"none"
			})
		})
	}
