const saveTableAllData = async (recordsAll)=>{
	let tableArr = [];
	for(let i in recordsAll){
		let findTable = false;
		for(let j in tableArr){
			if(tableArr[j].tmpId == recordsAll[i].tmpId && tableArr[j].pageId == recordsAll[i].pageId){
				tableArr[j].records.push(recordsAll[i]);
				findTable = true;
				break;
			}
		}
		if(!findTable){	
			tableArr.push({
				tmpId:recordsAll[i].tmpId,
				pageId:recordsAll[i].pageId,
				recordsAll:[recordsAll[i]]
			})
		}
	}
	if(tableArr && tableArr.length>0){
		for(let k in tableArr){
			let data = await saveOneTable(tableArr[k]);			
		}
		return Promise.resolve({isUpdate:true});
	}		
}
// {recordsAll:recordsAll,tmpId:tmpId,pageId:pageId}
const saveOneTable = async (params)=>{
	let result = await getSaveData(params.recordsAll,params.tmpId,params.pageId).catch(error=>{
		uni.showToast({
			title:'',
			icon:"none"
		})		
	});
	//result
	let isUpdate = false; //		
	//
	if(result.upData && result.upData.records && result.upData.records.length>0){
		 await updateData(result.upData).then(res=>{
			isUpdate = true;
			uni.showToast({
				title:"",
				icon:"none"
			})
		}).catch(error=>{});	
	}
	//
	if(result.addData && result.addData.length>0){
		let res = await addTable(result.addData).catch(error=>{});
		if(res){
			isUpdate = true;
			uni.showToast({
				title:"",
				icon:"none"
			})
		}							
	}
	return Promise.resolve({isUpdate:isUpdate})
}
// 
const getSaveData = (recordsAll,tmpId,pageId)=>{
	// console.log(recordsAll)
	let upData = {tmpId:tmpId,pageId:pageId,records:[]};
	let addData = [];
	recordsAll.forEach(v=>{
		// 
		if(v.pageId == pageId && v.tmpId == tmpId && v.tableId >= 0){		
			let findTableId = false;
			for(let j=0;j<upData.records.length;j++){ 
				if(upData.records[j].tableId === v.tableId){ //recordstableId 
					let findColumnId = false;
					for(let k=0;k<upData.records[j].updateColumnIds.length;k++){ 
						if( upData.records[j].updateColumnIds[k] === v.columnId){ //recordscolumnId  	
							upData.records[j].updateValues[k] = v.value;
							if(v.userRel){
								let findUsersRel = false;
								for(let s=0;s<upData.records[j].multiUserRel.length;s++){
									if(upData.records[j].multiUserRel[s].cid === v.columnId 
									&& upData.records[j].multiUserRel[s].tableId === v.tableId){
										upData.records[j].multiUserRel.splice(s,1,v.userRel);
										findUsersRel = true;
										break;
									}
								}
								if(!findUsersRel){
									if(upData.records[j].multiUserRel && Array.isArray(upData.records[j].multiUserRel)){
										upData.records[j].multiUserRel.push(v.userRel);
									}else{
										upData.records[j].multiUserRel = [v.userRel];
									}	
								}
								//
							}	
							findColumnId = true;
							break;
						}
					}
					if(!findColumnId){
						upData.records[j].updateColumnIds.push(v.columnId);
						upData.records[j].updateValues.push(v.value);
						if(v.userRel){
							if(upData.records[j].multiUserRel && Array.isArray(upData.records[j].multiUserRel)){
								upData.records[j].multiUserRel.push(v.userRel);
							}else{
								upData.records[j].multiUserRel = [v.userRel];
							}					
						}	
					}
					//		
					findTableId = true;
					break;
				}
			}
			if(!findTableId){
				if(v.userRel){
					upData.records.push({
						tableId:v.tableId,
						updateColumnIds:[v.columnId],
						updateValues:[v.value],
						multiUserRel:[v.userRel]
					})
				}else{
					upData.records.push({
						tableId:v.tableId,
						updateColumnIds:[v.columnId],
						updateValues:[v.value]
					})
				}		
			}
			// 
		}
		//
		if(v.pageId == pageId && v.tmpId == tmpId && v.tableId < 0 && v.value != ''){
			let findTableId = false;
			for(let j=0;j<addData.length;j++){ 
				if(addData[j].tableId === v.tableId){ //recordstableId row
					let findColumnId = false;
					for(let k=0;k<addData[j].params.length;k++){ 
						if( addData[j].params[k].columnId === v.columnId){ //recordscolumnId  	
							addData[j].params[k].value = v.value;
							if(v.userRel){
								let findUsersRel = false;
								for(let s=0;s<addData[j].multiUserRel.length;s++){
									if(addData[j].multiUserRel[s].cid === v.columnId 
									&& addData[j].multiUserRel[s].tableId === v.tableId){
										addData[j].multiUserRel.splice(s,1,v.userRel);
										findUsersRel = true;
										break;
									}
								}
								if(!findUsersRel){
									 addData[j].multiUserRel.push(v.userRel)
								}
								//
							}	
							findColumnId = true;
							break;
						}
					}
					if(!findColumnId){
						addData[j].params.push({
							columnId:v.columnId,
							value:v.value
						})
						if(v.userRel){
							if(addData[j].multiUserRel){
								addData[j].multiUserRel.push(v.userRel)
							}else{
								addData[j].multiUserRel = [];
								addData[j].multiUserRel.push(v.userRel)
							}
						}
					}
					//		
					findTableId = true;
					break;
				}
			}
			if(!findTableId){
				let obj={
					tmpId:v.tmpId,
					pageId:v.pageId,
					tableId:v.tableId,
					params:[{columnId:v.columnId,value:v.value}]
				}
				if(v.userRel){
					obj.multiUserRel = [v.userRel];
				}
				if(v.tmpData.parentTable){
					// console.log(v.parentTable);
					obj.childrenColumnid = v.tmpData.parentTable.childrenColumnid;
					obj.parentTmpid = v.tmpData.parentTable.parentTmpid;
					obj.parentTableid = Number(v.tmpData.parentTable.parentTableid);
				}
				addData.push(obj)
			}
			//	
		}//
	})
	//
	return Promise.resolve({upData:upData,addData:addData})	
}//

