 // [{"tmpId":1174,"reltmpid":"1150","tableId":146,"row":-1,"params":[{"columnId":6031,"value":"/"}]}]
const addMixed = (params)=>{
	params = JSON.stringify(params)
	// console.log(params)
	return $http.request({
		url:$api.addMixed,
		method:'POST',
		data:params
	}).then(value=>{
		return new Promise((resolve,reject)=>{
			if(value.data.code===0){
				console.log("www")
				resolve(true)
			}else{
				let msg = res.data.msg || '';
				uni.showToast({
					title:msg,
					icon:"none"
				})
				reject()
			}
		}) 	
	}).catch(error=>{})
}
