const rules = {
				username:{
					rule:/\S/,
					msg:""
				},
				password:[
					{
						rule:/\S/,
						msg:""
					},
					{
						rule:/^[0-9a-zA-Z]{6,16}$/,
						msg:"6-16"
					}
				],
				yuming:{
					rule:/\S/,
					msg:""
				},
				//
				website:[
					{
						rule:/\S/,
						msg:""
					},
					{
						// (1)http/https/ftp/ftps
						// (2)
						rule:/^((ht|f)tps?):\/\/[\w\-]+(\.[\w\-]+)+([\w\-.,@?^=%&:\/~+#]*[\w\-@?^=%&\/~+#])?$/,
						msg:""
					}
				],
				tel:{
					rule:/^0\d{2,3}-?\d{7,8}$/,
					msg:''
				},
				phone:{
					rule:/^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1}))+\d{8})$/,
					msg:''
				},
				email:{
					rule:/^[A-Za-zd]+([-_.][A-Za-zd]+)*@([A-Za-zd]+[-.])+[A-Za-zd]{2,5}$/,
					msg:''	
				}
			}
const validate = (key,content)=>{
	let bool = true;
	//key 
	if(!rules[key]){
		uni.showToast({
			title:"",
			icon:"none"
		})
		bool = false;
		return false
	}
	//
	if(rules[key] && Array.isArray(rules[key])){
		for(let i in rules[key]){
			if( !rules[key][i].rule.test(content) ){
				uni.showToast({
					title:rules[key][i].msg,
					icon:"none"
				})
				bool = false;
				return false
			}	
		}
		if(bool){
			return bool;
		}
	}else{
		if( !rules[key].rule.test(content) ){
			uni.showToast({
				title:rules[key].msg,
				icon:"none"
			})
			bool = false;
			return false
		}else{
			return bool;
		}	
	}	
}
const validateLoop = (form)=>{
	let alldone = true;
	for(let key in form){
		if(!validate(key,form[key])){
			alldone = false;
			break;
			return Promise.reject();
		};
	}
	if(alldone){
		return Promise.resolve(true)
	}		
}

export default validateLoop